  components: {
    IonPage,
    IonHeader,
    IonToolbar,
    IonThumbnail,
    IonButtons,
    IonContent
  },
  setup() {
    const {
      isAuthed
    } = useAuthuser();
    const {
      getUser
    } = useUser();
    return {
      isAuthed,
      getUser
    };
  }
});