import { defineComponent, ref } from "vue";
import { IonTabBar, IonTabButton, IonTabs, IonPage, IonRouterOutlet, onIonViewDidLeave } from "@ionic/vue";
import { newspaperOutline, personCircleOutline, bookmarkOutline, pricetagOutline, searchOutline } from "ionicons/icons";
export default defineComponent({
  name: "TabsPage",
  components: {
    IonTabs,
    IonTabBar,
    IonTabButton,
    IonPage,
    IonRouterOutlet
  },
  setup() {
    const iconSrc = ref("MagnifyingGlassPlus");
    const selliconSrc = ref("Tag");
    const saveiconSrc = ref("BookmarkSimple");
    const blogiconSrc = ref("Note");
    const accounticonSrc = ref("UserCircle");
    const afterTabChange = e => {
      if (e.tab == "SearchCar") {
        iconSrc.value = "MagnifyingGlassPlus-active";
      } else {
        iconSrc.value = "MagnifyingGlassPlus";
      }
      if (e.tab == "SellCar") {
        selliconSrc.value = "Tag-active";
      } else {
        selliconSrc.value = "Tag";
      }
      if (e.tab == "Saved") {
        saveiconSrc.value = "BookmarkSimple-active";
      } else {
        saveiconSrc.value = "BookmarkSimple_gray";
      }
      // if (e.tab == "Blogs") {
      //   blogiconSrc.value = "Note-active";
      // } else {
      //   blogiconSrc.value = "Note";
      // }
      if (e.tab == "Account") {
        accounticonSrc.value = "UserCircle-active";
      } else {
        accounticonSrc.value = "UserCircle";
      }
    };
    let backBtnListener;
    onIonViewDidLeave(() => {
      // console.log(backBtnListener);
      // backBtnListener.unregister();
      console.log("unregister");
    });
    return {
      bookmarkOutline,
      pricetagOutline,
      searchOutline,
      newspaperOutline,
      personCircleOutline,
      afterTabChange,
      iconSrc,
      selliconSrc,
      saveiconSrc,
      blogiconSrc,
      accounticonSrc
    };
  }
});